<template>
  <div class="box">
    <header>
      <van-icon name="arrow-left" @click="$router.go(-1)" />
      <h3>修改个人信息</h3>
      <span @click="baocun">保存</span>
    </header>
    <div>
      <!-- 昵称 -->
      <template v-if="tag == 'nickname'">
        <input
          type="text"
          placeholder="最多可输入15个字"
          maxlength="15"
          v-model="value"
        />
      </template>
      <!-- 性别 -->
      <template v-if="tag == 'sex'">
        <ul>
          <li @click="value = 0" :class="value == 0 ? 'active' : ''">男</li>
          <li @click="value = 1" :class="value == 1 ? 'active' : ''">女</li>
        </ul>
      </template>
      <!-- 学课 -->
      <template v-if="tag == 'subjects'">
        <div class="subjects">
          <ul>
            <li v-for="item in arr" :key="item.id">
              <input
                type="checkbox"
                name=""
                id=""
                :checked="str.includes(item.id)"
                class="check"
                @click="checked(item.id, $event)"
              />
              <span>{{ item.name }}</span>
            </li>
          </ul>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  // 组件名称
  name: "demo",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      tag: this.$route.query.tag,
      value: this.$route.query.value,
      arr: [
        { id: 7, name: "语文" },
        { id: 8, name: "数学" },
        { id: 9, name: "英语" },
        { id: 12, name: "物理" },
        { id: 13, name: "化学" },
        { id: 24, name: "生物" },
        { id: 25, name: "政治" },
        { id: 26, name: "信息技术" },
      ],
      str: "",
      arr1: [],
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    // 修改用户名保存
    async baocun() {
      if (this.tag == "nickname") {
        let { data } = await this.$http.ok_up_user({ nickname: this.value }); //   修改用户名
        data.code == 200 ? this.$toast("改名成功") : this.$toast(data.msg);
      } else if (this.tag == "sex") {
        let { data } = await this.$http.ok_up_user({ sex: this.value }); //  修改性别
        data.code == 200 ? this.$toast("修改性别成功") : this.$toast(data.msg);
      } else if ((this.tag = "subjects")) {
        let { data } = await this.$http.ok_up_user({
          user_attr: JSON.stringify(this.arr1),
        });
      }
      this.$router.push("/geren");
    },
    // 选中的科目
    checked(id, event) {
      let obj = { attr_id: 2, attr_val_id: id };
      if (event.target.checked) {
        let flag = JSON.stringify(this.arr1).indexOf(JSON.stringify(obj));
        if (flag == -1) {
          this.arr1.push(obj);
        }
      } else {
        let index = this.arr1.findIndex((item) => {
          return item.attr_val_id == id;
        });
        this.arr1.splice(index, 1);
      }
    },
  },
  created() {},
  mounted() {
    this.$route.query.select.map((item) => {
      if (item.attr_id == 2) {
        this.arr1.push({ attr_id: 2, attr_val_id: item.attr_val_id });
        return (this.str += item.attr_val_id + ",");
      } else {
        this.arr1.push({ attr_id: 1, attr_val_id: item.attr_val_id });
      }
    });
  },
};
</script> 

<style lang="scss" scoped>
.box {
  height: 100%;
  background: #f0f2f5;
  header {
    height: 11.73333vw;
    z-index: 999;
    width: 100%;
    background: #fff;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    padding: 0 2.66667vw;
    align-items: center;
    font-size: 4vw;
    h3 {
      font-size: 4.8vw;
      font-weight: 400;
    }
  }
  > div {
    margin-top: 3vw;
    // 昵称
    input {
      display: block;
      box-sizing: border-box;
      width: 100%;
      min-width: 0;
      line-height: 12.8vw;
      height: 12.8vw;
      text-indent: 4vw;
      margin: 0;
      padding: 0;
      color: #323233;
      text-align: left;
      background-color: transparent;
      border: 0;
      resize: none;
      font-size: 4vw;
      background-color: #fff;
    }
    // 性别
    ul {
      background: #fff;
      margin: 0.26667rem 0;
      margin: 2.66667vw 0;
      padding: 0.13333rem 0.4rem;
      padding: 1.33333vw 4vw;
      li {
        height: 14.66667vw;
        line-height: 1.46667rem;
        line-height: 14.66667vw;
        font-size: 0.37333rem;
        font-size: 3.73333vw;
        color: #595959;
        position: relative;
      }
      :nth-child(1):after {
        content: "";
        display: block;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 100%;
        height: 1px;
        background-color: #f5f5f5;
      }
      .active {
        background: url(data:image/png;base64,)
          no-repeat 100%;
        background-size: 0.4rem 0.4rem;
        background-size: 4vw 4vw;
      }
    }
    // 学课
    .subjects {
      ul {
        li {
          height: 14.66667vw;
          line-height: 14.66667vw;
          font-size: 3.73333vw;
          color: #595959;
          input {
            width: 6.66667vw;
            height: 6.66667vw;
            margin-top: 4vw;
            margin-right: 2.66667vw;
            float: left;
          }
          span {
            display: inline-block;
            height: 14.66667vw;
            line-height: 14.66667vw;
          }
        }
      }
      li:after {
        content: "";
        display: block;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 100%;
        height: 1px;
        background-color: #f5f5f5;
      }
    }
  }
}
</style>
